@import '../libs/vars';
@import '../libs/functions';
@import '../libs/mixins';

$color-sand: #F7EFE4;

.logo {
    width: 90px;
    height: 25px;
    background-image: url('/images/logo-nb.png');
    background-repeat: no-repeat;
    background-position: center center;
    display: inline-block;
    vertical-align: middle;
    margin-right: 5px;
    margin-top: -10px;
}

.main-title span {
    @include breakpoint(xmedium) {
        display:none;
    }
    @include breakpoint(medium) {
        display:inline;
    }
    @include breakpoint(xxsmall) {
        display:none;
    }
}

.noborder {
    border:none;
}

.noborder::after {
    border:none;
}

.anchor-offset {
    padding-top: 120px;
    margin-top: -120px;
    display: block;
    border:none;
    transition:none;
}

.wrapper.style2 {
    background-color: $color-sand;
    background-size: cover;
}



section#contact {
    background-color: $color-sand;
    input[type=text],input[type=email], input[type=password], textarea {
        background-color: white;
    }
    .icon-wrapper {
        background-color:white;
        box-shadow: 0 -1px 0 0 white, 0 1px 0 0 white;
        &:before {
            border-bottom-color: white;
        }
        &:after {
            border-top-color: white;
        }
    }
}

.Page__index section#contact {
    background-color: white;
    input[type=text],input[type=email], input[type=password], textarea {
        background-color: $color-sand;
    }
    .icon-wrapper {
        background-color:$color-sand;
        box-shadow: 0 -1px 0 0 $color-sand, 0 1px 0 0 $color-sand;
        &:before {
            border-bottom-color: $color-sand;
        }
        &:after {
            border-top-color: $color-sand;
        }
    }
}

section#banner {
    background-size: 100%;
    background-position: center center;
    background-image: url(data:image/jpeg;base64,);
    background-attachment: scroll;
    background-color:white !important;
    background-repeat: no-repeat;

    @include breakpoint(medium) {
        background-size: cover;
    }
}


// hack to show the first slide b64
section#banner::after {
    background-color:transparent !important;
}
section#banner article::after {
    background:none !important;
}
section#banner h2 {
    font-family: 'Dancing Script', cursive;
    font-size:4.75em;
    font-weight:normal;
    text-transform: none;
}

.Flag__fr, .Flag__en {
    display: inline-block;
    width: 20px;
    height: 11px;
    background-size: 100% auto;
    margin-right: 5px;
    -webkit-filter: grayscale(100%) brightness(2);
    filter: grayscale(100%) brightness(2);
}

.Flag__fr {
    background-image: url('/images/flag-fr.png');
}
.Flag__en {
    background-image: url('/images/flag-en.png');
}
// tarifs
