.tooltip-block {
    position:relative;
    cursor:pointer;
}
.tooltip-block:hover:after{
    border: 1px solid darken($color-sand, 20%);
    padding: 3px 6px;
    background: $color-sand;
    content: attr(data-title);
    position: absolute;
    right: -210px;
    top: -45px;
    width: 200px;
    font-family: Helvetica, Arial;
    font-size: 12px;
}

.tarif__title {
    font-size: 1.6em
}


.tarif__category {
    font-size: 1em;
    text-align:center;
}


